@charset "UTF-8";
a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Sarala";
  src: url("../fonts/saralaregular.woff2") format("woff2"), url("../fonts/saralaregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Sarala";
  src: url("../fonts/saralabold.woff2") format("woff2"), url("../fonts/saralabold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.flights {
  display: grid;
  grid-template-columns: 254px 1fr;
  column-gap: 23px;
  max-width: 917px;
  margin: 0 auto;
}

.flights__container {
  padding: 20px 0;
}

.flights__list {
  padding: 0;
  margin: 0 0 20px;
  list-style: none;
}

.flights-list__item:not(:last-child) {
  margin-bottom: 28px;
}

.flights__show-more {
  display: block;
  min-width: 255px;
  margin: 0 auto;
  padding: 5px 10px;
  font-family: "Tahoma", sans-serif;
  font-size: 18px;
  background: #f1f1f1;
  border: 1px solid #575454;
  box-shadow: 0 0 0 1px #a6a5a5 inset;
}
.flights__show-more:hover {
  cursor: pointer;
}
.flights__show-more:active {
  padding: 6px 10px 4px;
}

.filter {
  height: min-content;
  padding: 34px 0 34px 20px;
  font-family: "Verdana", sans-serif;
  font-size: 12px;
  border-top: 36px solid #d9d9d9;
  border-bottom: 144px solid #d9d9d9;
}

.filter__fieldset {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  border: none;
}
.filter__fieldset:not(:last-child) {
  margin-bottom: 36px;
}

.filter__legend {
  margin-bottom: 16px;
  font-weight: 700;
}

.filter__label {
  display: flex;
  align-items: center;
}
.filter__label:not(:last-child) {
  margin-bottom: 2px;
}
.filter__label--text:not(:last-child) {
  margin-bottom: 17px;
}
.filter__label--text .filter__field-caption {
  margin-right: 6px;
}

.filter__input-radio,
.filter__input-checkbox {
  margin: 0 5px 0 0;
}

.filter__input-checkbox {
  position: relative;
}
.filter__input-checkbox::before {
  content: attr(data-instances-count);
  position: absolute;
  top: 0;
  right: 175%;
}

.filter__field-caption {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.filter__carrier-option {
  display: grid;
  grid-template-columns: 142px auto;
  column-gap: 5px;
  align-items: center;
}

.flight__top-line {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto auto;
  padding: 6px 15px 6px 6px;
  line-height: 1;
  color: #ffffff;
  background-color: #0087c9;
}

.flight__carrier-logo {
  grid-column: 1/2;
  grid-row: 1/3;
}

.flight__cost {
  grid-column: 2/3;
  grid-row: 1/2;
  margin: 0;
  font-size: 20px;
  text-align: right;
}

.flight__cost-caption {
  grid-column: 2/3;
  grid-row: 2/3;
  margin: 0;
  font-size: 11px;
  text-align: right;
}

.flight__leg {
  padding: 0 8px 8px;
}
.flight__leg:not(:last-child) {
  border-bottom: 2px solid #0083c3;
}

.flight__path {
  margin: 0;
  padding: 6px 11px;
  font-size: 18px;
  border-bottom: 1px solid #e5e5e5;
}

.flight__airport-code {
  color: #0087c9;
  font-size: 16px;
}
.flight__airport-code--from::after {
  content: " ‚ü∂";
  font-weight: 400;
}

.flight__timing {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 11px 11px 0;
}

.flight__datetime {
  margin: 0;
}

.flight__time {
  font-size: 20px;
}

.flight__date {
  font-size: 16px;
  color: #0087c9;
}

.flight__duration {
  margin: 0;
  font-size: 18px;
}
.flight__duration::before {
  content: url("/img/clock.png");
  margin-right: 4px;
}

.flight__changes {
  position: relative;
  width: 85%;
  min-height: 23px;
  margin: 0 auto 8px;
  text-align: center;
  color: #ff8e1d;
}
.flight__changes::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 1px;
  background: #a2a2a2;
}

.flight__changes-text {
  position: relative;
  z-index: 2;
  padding: 0 10px;
  background: #ffffff;
}

.flight__carrier-name {
  margin: 0;
  padding: 0 10px;
}

.button {
  width: 100%;
  padding: 10px;
  font-family: "Tahoma", sans-serif;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #ffb168;
  border: none;
}
.button:hover {
  cursor: pointer;
}

.page {
  height: 100%;
}

.page__body {
  margin: 0;
  font-family: "Tahoma", sans-serif;
  line-height: 1.45;
  color: #1c1c1c;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}